.home {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	overflow-y: auto;
	padding-top: 1em;
}

.album_grid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	margin-top: 2em;
	margin-bottom: 8rem;
	justify-content: space-between;
}

.singles_grid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	justify-content: space-between;
}

.singles_grid>h2 {
	width: 100%;
	padding-bottom: 1em;
}

/* ===============================================================================
MOBILE RULES
=============================================================================== */

@media (max-width: 768px) {
	.home {
		padding: 1em;
	}
	.album_grid {
		flex-direction: column;
		align-items: center;
	}
}
