.modal {
	background-color: #0F141D;
	position: fixed;
	bottom: 0;
	left: 16%;
	right: 0;
	z-index: 1;
	padding-top: 1em;
	overflow: scroll;
	cursor: default;
}

.modal_header {
	padding: 0em 2em 0em 0.75em
}

.modal_content {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	padding: 1em;
}

.close_btn {
	font-size: 1em;
	font-weight: 800;
	padding: 0;
	color: #FFFFFF;
	background-color: transparent;
}

.close_btn>img {
	vertical-align: middle;
	margin-right: 0.5em;
	height: 1em;
	width: auto;
}

.image {
	height: 18em;
	width: 18em;
}

.info {
	display: inline-block;
	margin-left: 2em;
}

.info>h3 {
	margin: 1em 0;
	font-weight: normal;
}

.info>span {
	color: #62728E;
	font-size: 0.75em;
	text-transform: uppercase;
}

.info>button {
	display: flex;
	align-items: center;
	background-color: #00C8D4;
	color: #FFFFFF;
	border-radius: 8px;
	padding: 0.5em 2.5em;
	margin-top: 1.5em;
}

.info>button>img {
	height: 1em;
	width: auto;
	padding-right: 0.5em;
}

.table {
	width: 100%;
	margin-top: 2em;
	border-collapse: collapse;
	table-layout: fixed;
	margin-bottom: 8rem;
}

.table th {
	color: #83868B;
	font-size: 0.9em;
	padding: 1em 0;
	text-align: left;
}
.table th:first-child {
	width: 5%;
}

.table td {
	color: #D2D7DF;
	padding: 1em 0;
	text-align: left;
}

/* ===============================================================================
MOBILE RULES
=============================================================================== */

@media (max-width: 768px) {
	.modal {
    left: 0;
  }
	.table th:first-child {
		width: 10%;
	}
}

/* ===============================================================================
LARGE SCREENS RULES
=============================================================================== */

@media (min-width: 1250px) {
	.modal {
		left: 13%;
	}
}
