.navbar {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	padding: 1em;
	padding-right: 2em;
	position: sticky;
	top: 0;
	background-color: #0F141D;
	z-index: 5;
}

.navbar-right {
	display: flex;
	flex-direction: row;
	align-items: center;
}
.btn-navbar {
	background-color: transparent;
	color: #FFFFFF;
	text-decoration: none;
	margin-right: 1rem;
	margin-left: 1rem;
}

/* search bar */

.search-cont {
	display: flex;
	align-items: center;
}

.search-cont>img {
	height: 1.25em;
	width: auto;
}

.search-cont>input {
	padding: 0.5em;
	background-color: transparent;
	color: #62728E;
	border: none;
	min-width: 15em;
}

/* ===============================================================================
MOBILE RULES
=============================================================================== */

@media (max-width: 768px) {
	.navbar {
		flex-direction: column-reverse;
		padding: 1em;
	}
	.navbar-right {
		flex-direction: column-reverse;
	}
}
