.sidebar-wrapper {
	display: flex;
	flex-direction: column;
	width: 20%;
	height: 100%;
	padding-left: 2em;
	padding-right: 2rem;
	/* padding-top: 6.2em; */
	padding-top: 1.5rem;
}

.sideBtnDiv {
	display: flex;
	align-items: center;
	cursor: pointer;
	margin-bottom: 2.5em;
}
.logo {
	margin-bottom: 6.2rem;
}

/* ===============================================================================
MOBILE RULES
=============================================================================== */

@media (max-width: 768px) {
	.sidebar-wrapper {
		width: 100%;
		padding-right: 2rem;
	}
	.logo {
		margin-bottom: 1rem;
	}
	.sidebar {
		width: 100%;
		height: unset;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-around;
		padding-left: 0;
		padding-top: 1.5em;
	}

	.sideBtnDiv {
		margin-bottom: 1.5em;
	}
}

/* ===============================================================================
LARGE SCREENS RULES
=============================================================================== */

@media (min-width: 1250px) {
	.sidebar-wrapper {
		width: 15%;
	}
}
