.search_modal {
	height: 50%;
	background-color: rgba(31, 38, 49, 0.95);
  opacity: 1;
	position: fixed;
	left: 13%;
	right: 0;
	z-index: 1;
	padding-top: 1em;
	overflow: scroll;
	backdrop-filter: blur(6px);
	-webkit-backdrop-filter: blur(6px);
}

.search_modal_header {
	padding: 0em 2em 0em 0.75em
}

.search_modal_content {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	padding: 1em;
}

.search_modal_content>h3 {
	width: 100%;
	padding-bottom: 1em;
}

.search_grid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	justify-content: space-between;
}

.singles_grid>h2 {
	width: 100%;
	padding-bottom: 1em;
}

/* ===============================================================================
MOBILE RULES
=============================================================================== */

@media (max-width: 768px) {
	.search_modal {
		left: 0;
	}
}
