	padding: 0.5em 1em;
}

button,
a {
	cursor: pointer;
	border: none;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

button:focus,
a:focus {
	outline: none;
	border: 0;
}


button::-moz-focus-inner,
a::-moz-focus-inner {
	border: 0;
}

input,
