	outline-width: 0;
}

.modal {
	background-color: #0F141D;
	position: fixed;
	bottom: 0;
	left: 16%;
	right: 0;
	z-index: 1;
	padding-top: 1em;
	overflow: scroll;
	cursor: default;
}

.modal_header {
	padding: 0em 2em 0em 0.75em
}

.modal_content {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	padding: 1em;
}

