	font-size: 1em;
	font-weight: 800;
	padding: 0;
	color: #FFFFFF;
	background-color: transparent;
}

.close_btn>img {
	vertical-align: middle;
	margin-right: 0.5em;
	height: 1em;
	width: auto;
}

