.container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
}

.btn-outline {
  border: 1px solid #00C8D4;
  color: #00C8D4;
  background-color: transparent;
}
.input-group {
  width: 24rem;
  display: flex;
  flex-direction: column;
}
.input-group input {
  height: 40px;
  background-color: transparent;
  color: #FFFFFF;
  border: 1px solid rgb(48, 56, 71);
  border-radius: 8px;
  outline: none;
  padding: 4px 8px;
}
