.explore {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	overflow-y: auto;
	padding-top: 1em;
}

.explore_grid {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;
	margin-top: 2em;
	justify-content: space-between;
}

/* ===============================================================================
MOBILE RULES
=============================================================================== */

@media (max-width: 768px) {
	.explore {
		padding: 1em;
	}
	.explore_grid {
		/* flex-direction: column; */
		align-items: center;
	}
}
