.container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
}

.card-container {
  width: 30%;
  margin: 15px;
  text-align: center;
}

.music-pass {
  width: 100%;
  box-shadow: 0px 50px 300px 50px rgba(39,176,230,0.29);
}

.golden-pass {
  width: 100%;
  box-shadow: 0px 50px 300px 50px rgba(255,193,7,0.29);
}

.small {
  font-size: .8rem;
  margin-bottom: 0.5rem;
}

.btn-buy {
  font-weight: 800;
  border-radius: 8px;
  margin-top: 1rem;
  color: #FFFFFF;
  background-color: #00C8D4;
  cursor: not-allowed;
}
