.play_modal {
	position: fixed;
	bottom: 2em;
	left: 2em;
	right: 2em;
	z-index: 999;
	padding: 1em 3em;
	justify-content: space-between;
	align-items: center;
	background-color: rgba(31, 38, 49, 0.5);
	border-radius: 8px;
	opacity: 1;
	backdrop-filter: blur(6px);
	-webkit-backdrop-filter: blur(6px);
}

.play_modal div {
	display: flex;
	align-items: center;
}

.song_data>img {
	height: 4em;
	width: 4em;
}

.song_data>div {
	display: flex;
	flex-direction: column;
	margin-left: 1.5em;
	align-items: baseline;
}

.song_data div b {
	width: 180px;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;

}

.main_control {
	margin-right: 2em;
}

.main_control button {
	padding: 0;
	background-color: transparent;
	height: 1.5em;
}

.main_control img {
	height: 1.5em;
	width: auto;
}

.main_control button:nth-child(1)>img {
	transform: rotate(180deg);
}

.main_control button:nth-child(2) {
	margin: 0 1.5em;
	height: 2em;
	width: 2em;
}

.main_control button:nth-child(2)>img {
	height: 2em;
}

.time_tracker {
	display: inline-block !important;
	color: #62728E;
	font-size: 0.8em;
	width: 4em;
	text-align: center;
	background-color: transparent;
}

.timebar {
	height: 0.25em;
	display: flex;
	width: 30em;
	min-width: 10em;
	padding: 0 !important;
}

.mute_button {
	height: 1.15em;
	margin-right: 1em;
	padding: 0;
	background-color: transparent;
}

.mute_button>img {
	height: 1em;
	width: auto;
}

/* ===============================================================================
MOBILE RULES
=============================================================================== */

@media (max-width: 768px) {
	.play_modal {
		display: flex;
		flex-direction: column;
		padding: 0;
	}

	.play_modal>div, .play_modal>div>div {
		padding-top: 0.5rem;
		padding-bottom: 0.5rem;
	}

	.controls_wrapper {
		display: flex;
		flex-direction: column;
	}

	.timebar {
		width: 100%;
	}

	.volume_control {
		display: none !important;
	}
}

/* ===============================================================================
LARGE SCREENS RULES
=============================================================================== */

@media (min-width: 1250px) {
	.timebar {
		width: 35em;
	}
}
